/* --------------------------------------------------------- */
/* !Classes */
/* --------------------------------------------------------- */
.mtphr-dnt-clearfix:before,
.mtphr-dnt-clearfix:after {
  display: table;
  border-spacing: 0;
  content: " ";
}

.mtphr-dnt-clearfix:after {
