}

/* --------------------------------------------------------- */
/* !Code copy - 1.4.0 */
/* --------------------------------------------------------- */
#mtphr-dnt-code-copy table {
  display: block;
  width: 100%;
}
#mtphr-dnt-code-copy td {
  display: block;
  width: auto;
  vertical-align: top;
  color: hsl(0, 0%, 95%);
  background: hsl(0, 0%, 20%);
  border: 1px solid #dfdfdf;
  border-radius: 5px;
  border-top: 5px solid hsl(128, 52%, 57%);
  margin: 10px 0;
}
